import React from 'react';
import delete_bas from './../img/delete.svg';

class Library extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isOpen: false,
        translation: '',
        value: '',
        library: JSON.parse(localStorage.getItem('library')) || [{id: 0, word: 'привет', translate: 'hello'}]
      }
      this.wordsRef = Array(this.state.library.length)
      this.changMode = this.changMode.bind(this)
      this.getValue = this.getValue.bind(this)
      this.addWordToLibrary = this.addWordToLibrary.bind(this)
      this.checkWord = this.checkWord.bind(this)
    }

    componentDidMount() {
      document.addEventListener('keydown', (event) => {
        if (this.state.value.length > 0  && this.state.isOpen && event.key === 'Enter') {
          this.addWordToLibrary()
        }
      })
    }

    changMode () {
      this.setState(prevState => ({
        isOpen: !prevState.isOpen
  
      }))
      
      console.log(this.state.isOpen)
    }

    async removeWordFromLibrary(index) {
      await this.setState(prevState => ({
        library: prevState.library.filter((word, i) => i !== index)
      }))
      await localStorage.setItem('library', JSON.stringify(this.state.library))
    }

    async addWordToLibrary () {
      try {
        const response = await fetch(`http://tmp.myitschool.org/API/translate/?source=ru&target=en&word=${this.state.value}`, {
        "method": "GET"
        });
        const result = await response.json();
        if (result.translate) {
            await this.setState( () => ({
            translation: result.translate
      
          }))
        }
        await this.setState(prevState => ({
          library: [...prevState.library, {id: this.state.library.length, word: this.state.value, translate: this.state.translation, correct: 0, learn: 0, error: 0}]
        }))
        await localStorage.setItem('library', JSON.stringify(this.state.library))
        console.log(result.translate);
        await this.changMode();
        await this.setState( () => ({
          translation: ''
    
        }))
         
      }
      catch (error) {
        console.log(error)
      }
    }

    async getValue(event) {
      const value = event.currentTarget.value
      this.setState( () => ({
        value: value
  
      }))

      
    } 
    
    checkWord() {
      //let o = this.wordsRef[1]
    }

    render () {
      return (
        <div>
          <div className="add_word_container">
            {!this.state.isOpen ? 
            null : 
            <div>
              <input onChange={this.getValue} placeholder="Enter new word"/>
            <span>{this.state.translation}</span>
              <button onClick={this.addWordToLibrary} className="btn-round check"></button>
            </div>
            }
            <button onClick={this.changMode} className={this.state.isOpen ? "btn-round close" : "btn-round add"}></button>
          </div>

            <div className="library-container">
              <div className="library-header">
                <div>Word</div>
                <div>Translate</div>
                <div>Learn level</div>
              </div>
              {this.state.library.map((word, index) => (
                <div key={index}
                ref={el => this.wordsRef[index] = el}
                >
                  <div>
                  {word.id}
                  </div>
                  <div>
                    {word.word}
                  </div>
                  <div>
                    {word.translate}
                  </div>
                  <div onClick={() => this.removeWordFromLibrary(index)} className="delete"> <div>Delete</div><img src={delete_bas} /> </div>
                </div>
                
              ))}
              
            </div>
            {/* <button onClick={this.checkWod}>Check the word</button> */}
        </div>
      )
    }
  
}

export default Library